.objective-console {
    position: absolute;
    top: 100vh;
    left: 0;
    width: 100%;
    height: 80vh;
    background-color: black;
}

.objective-wrapper {
    width: 100%;
    height: 100%;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.objective-blurb h1 {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    font-size: 5em;

}

.objective-blurb p {
    font-family: 'Roboto Condensed', sans-serif;
}

.objective-list {
    display: flex;
    flex-direction: column;
}

.objective-list ul {
    list-style-type: none;
}

.objective-link {
    font-family: 'Roboto Condensed', sans-serif;
    cursor: pointer;
}