.portfolio-console {
    position: absolute;
    top: 300vh;
    left: 0;
    background-color: rgb(26, 26, 26);
    width: 100%;
    height: 100vh;
}

.portfolio-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    column-gap: 20px;
    justify-content: center;
    align-items: center;
    color: white;
}

.portfolio-wrapper img {
    width: 100%;
    max-width: 40em;
    border-radius: 20px;
}