.contact-console {
    position: absolute;
    top: 700vh;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #8EC5FC;
    background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);
}

.contact-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    height: 100%;
    width: 100%;
}

.contact-wrapper h1 {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
	font-size: 5em;
	color: transparent;
	text-shadow: 0px 0px 2px whitesmoke;
	-webkit-text-stroke-width: 1.5px;
    -webkit-text-stroke-color: black;
}


form {
    font-family: 'Roboto Condensed', sans-serif;
    padding: 0 35% 0 35%;
    display: flex;
    flex-direction: column;
    text-align: left;
    row-gap: 10px;
}

input {
    background-color: whitesmoke;
}


button {
    height: 2em;
    width: 7em;
    border-radius: 25px;
}

span .visible {
    visibility: visible;
}

span {
    visibility: hidden;
}




