.about-console {
    position: absolute;
    top: 200vh;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgb(26, 26, 26);
}

.about-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
}

.about-images:nth-child(1) {

}
.about-images img {
    width: 100%;
    max-width: 10em;
    border: 2px solid whitesmoke;
    border-radius: 50%;
}


.about-blurb h1 {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
	font-size: 4em;
    background: -webkit-linear-gradient(-91deg, #ba2fff  5%, #df99ff 53%, #ff4bc9  91%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.about-blurb p {
    color: whitesmoke;
}




/*====MOBILE====*/
@media only screen and (max-width: 1200px) { 
    .about-console {
        top: 250vh;
    }
}  


@media only screen and (max-width: 950px) { 
    .about-console {
        top: 280vh;
    }
}