.point-one-console {
    position: absolute;
    top: 180vh;
    left: 0;
    width: 100%;
    height: 100vh;
    background-image: url("../../../public/images/taylor-point-one.jpg");
    background-size: cover;
    background-repeat: no-repeat; 
    background-color: #000000;
    color: black;
}

.point-one-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 15em;
}

.point-one-blurb {
    padding-right: 10em;
}

.point-one-chart dt {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
	font-size: 3em;
	color: transparent;
	text-shadow: 0px 0px 2px whitesmoke;
	-webkit-text-stroke-width: 1.5px;
    -webkit-text-stroke-color: black;
}


dl {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 700px;
    position: relative;
    padding: 20px;
  }
  
  dt {
    align-self: flex-start;
    width: 100%;
    font-weight: 700;
    display: block;
    text-align: center;
    font-size: 1.2em;
    font-weight: 700;
    margin-bottom: 20px;
    margin-left: 130px;
  }
  
  .text {
    font-family: 'Roboto Condensed', sans-serif;
    height: 40px;
    width: 130px;
    color: black;
    justify-content: flex-end;
  }
  
  .percentage {
    font-size: 0.8em;
    line-height: 1;
    text-transform: uppercase;
    width: 100%;
    height: 40px;
    margin-left: 130px;
  }

  .percentage:after {
    content: "";
    display: block;
    background-color: #3d9970;
    width: 50px;
    margin-bottom: 10px;
    height: 90%;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    transition: background-color 0.3s ease;
    cursor: pointer;
  }

  .percentage:hover:after, .percentage:focus:after {
    background-color: #aaa;
  }
  
  
  .percentage-15:after {
    width: 15%;
  }
  

  .percentage-20:after {
    width: 20%;
  }
  
  .percentage-30:after {
    width: 30%;
  }

  .percentage-40:after {
    width: 40%;
  }
  
  .percentage-50:after {
    width: 50%;
  }
  
  .percentage-60:after {
    width: 60%;
  }

  .percentage-70:after {
    width: 70%;
  }

  
  .percentage-80:after {
    width: 80%;
  }
  
  
  .percentage-90:after {
    width: 90%;
  }
