.intro-console {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-image: url("../../../public/images/taylor-intro.jpg");
    background-size: cover;
    background-repeat: no-repeat; 
    background-color: #000000;
}


.intro-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-items: center;
    color: white;
    background-color: rgba(0, 0, 0, 0.644);
}

.intro-blurb {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 75%;
}

.intro-blurb {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
	color: transparent;
	text-shadow: 0px 0px 2px whitesmoke;
	-webkit-text-stroke-width: 1.5px;
    -webkit-text-stroke-color: black;
}

.intro-blurb h1 {
    font-size: 5em;
}

.intro-blurb h3 {
    font-size: 2em;
}